<template>
  <div>

 <nav class="col-md-4 col-lg-3 d-md-block mt-5 cusbar" >
            <div class="list-group list-group-flush">
              <span data-feather="home" class="nav-link fas text-info pb-3"><i class="fas fa-box"> 商品列表 </i></span>

              <a class="list-group-item nav-link side-li active"
                data-toggle="list" href="#"
                @click.prevent="currentPage ='全部',status.filter=true">
                 全部
              </a>
              <a href="#" class="list-group-item nav-link side-li" data-toggle="list"
                @click.prevent="currentPage ='台灣',status.filter=true ">
                台灣
              </a>
              <a href="#" class="list-group-item nav-link side-li" data-toggle="list"
                @click.prevent="currentPage ='國外',status.filter=true" >
                國外
              </a>
              <a href="#" class="list-group-item nav-link side-li" data-toggle="list"
                @click.prevent="currentPage ='其他',status.filter=true" >
                其他
              </a>
            </div>
        </nav>

    <main role="main" class="col-md-9 ml-sm-auto col-lg-9" style="position: absolute; left: 250px;">
      
            <div class="text-center mb-3" style="background-color:#e3e6e8; width:100%; height:50px; border-radius:5px;"><h3 class="pt-1">商品選購</h3></div>
    <loading :active.sync="isLoading"></loading>
    
    <div class="dropdown">
      <Customcart/>
    </div>

    <!-- 商品卡片modal -->
    <div class="row mt-4">
      <div class="col-lg-4 col-md-6 mb-4" v-for="item in filterProducts" :key="item.id">
        <div class="card border-0 shadow-sm">
          <div class="imghover" @click="goDetail(item.id)">
          <div style="height: 150px; background-size: cover; background-position: center" :style="{backgroundImage:`url(${item.imageUrl}`}"></div></div>
          <div class="card-body" style="background-color: azure;">
            <span class="badge badge-secondary float-right ml-2">{{item.category}}</span>
            <h5 class="card-title">
              <span class="text-dark">{{item.title}}</span>
            </h5>
            <p class="card-text">{{item.content}}</p>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h5" v-if="!item.price">{{item.origin_price | currency}}</div>
              <!-- 顯示僅有原價 -->
              <del class="h6" v-if="item.price">{{item.origin_price | currency}}</del>
              <div class="h5" v-if="item.price">{{item.price | currency}}</div>
              <!-- 顯示原價 & 優惠價 -->
            </div>
          </div>
          <div class="card-footer d-flex">
            <button type="button" class="btn btn-outline-secondary btn-sm" @click="goDetail(item.id)">
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
              查看更多
            </button>
            <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addtocart(item.id)">
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
              加到購物車
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- 查看更多 Modal -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog" >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <img :src="product.imageUrl" class="img-fluid" alt>
            <blockquote class="blockquote mt-3">
              <p class="mb-0">{{ product.content }}</p>
              <footer class="blockquote-footer text-right">{{ product.description }}</footer>
            </blockquote>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h4" v-if="!product.price">{{ product.origin_price | currency}} 元</div>
              <del class="h6" v-if="product.price">原價 {{ product.origin_price | currency}} 元</del>
              <div class="h4" v-if="product.price">現在只要 {{ product.price | currency}} 元</div>
            </div>
            <select name class="form-control mt-3" v-model="product.num">
              <option :value="num" v-for="num in 10" :key="num">選購 {{num}} {{product.unit}}</option>
            </select>
          </div>
          <div class="modal-footer">
            <div class="text-muted text-nowrap mr-3">
              小計
              <strong>{{ product.num * product.price | currency }}</strong> 元
            </div>
            <button type="button" class="btn btn-primary" @click="addtocart(product.id, product.num)">
              <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i>
              加到購物車
            </button>
          </div>
        </div>
      </div>
    </div>

    <div class="d-flex justify-content-end"  v-if="!status.filter">
       <Pagination class="mx-auto" :pages="pagination" @emitPages="getproduct" />
    </div>
   </main>     
  </div>
</template>

<script>
import Customcart from './Customcart';
import Pagination from '../Pagination';
import $ from "jquery";

export default {
  data() {
    return {
      allProducts:[],
      products: [],
      currentPage:"全部",
      pagination: {},
      product: {},
      cart: [],
      carts: [],
      status: {
        loadingItem: "",
        filter:false,
      },
      form:{
        user:{
          name:'',
          email:'',
          tel:'',
          address:'',
        },
        message:'',
      },
      isLoading: false,
      salecode:'',
    };
  },
  methods: {
    getproduct(page=1) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/products?page=${page}`;

      vm.isLoading = true;
      this.$http.get(url).then(response => {
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
        // console.log(response.data);
        vm.isLoading = false;
      });
    },
    getAllProducts(){
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/products`;
      this.$http.get(api).then(response => {
        this.allProducts=response.data.products;
      });
    },
    getone(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;

      vm.status.loadingItem = id;
      this.$http.get(url).then(response => {
        vm.product = response.data.product;
        $("#productModal").modal("show");
        // console.log(response);
        vm.status.loadingItem = "";
      });
    },
    goDetail(id) {
        this.$router.push(`/customer/detail/${id}`);
    },
    addtocart(id, qty = 1) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.status.loadingItem = id;
      const cart = {
        product_id: id,
        qty
      };

      this.$http.post(url, { data: cart }).then(response => {
        vm.status.loadingItem = "";
        // console.log(response)
         this.$bus.$emit("message:push", response.data.message, "success");
        vm.getCart();
        $("#productModal").modal("hide");
      });
    },
    getCart() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      this.$http.get(url).then(response => {
        vm.carts = response.data.data;
        vm.cart = response.data.data.carts;
        vm.isLoading = false;
      });
    },
    delcart(id){
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(url).then(response => {
      // console.log(response)
      this.getCart();
        vm.isLoading = false;
      });
    },
    addsalecode(){
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
      const coupon ={ code : vm.salecode }
      //讓API回傳的code=設定的變數

      vm.isLoading = true;
      this.$http.post(url , {data:coupon}).then(response => {
        console.log(response)
        console.log(response.data.message)
      if(response.data.success==false){
        this.$bus.$emit("message:push", response.data.message, "danger");
        // alert(response.data.message)
      }
      vm.getCart();
        vm.isLoading = false;
      });
    },
    creatorder(){
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const order = vm.form;

      this.$validator.validate().then((result) => {
          if (result) {
              this.$http.post(url , {data:order}).then(response => {
              // console.log('訂單已建立', response);
              if(response.data.success){
                vm.$router.push(`/customer_payment/${response.data.orderId}`)
              }
               vm.isLoading = false;
            });
        }else{
          // console.log('欄位不完整')
        }
      });
    },
  },
  computed: {
    filterProducts() {
      var vm = this;
      
      if (vm.currentPage == "全部") {
        return this.products;
      }
      else{
        return this.allProducts.filter(function(item, index) {
          if (item.category == vm.currentPage) return true;
        });
      }
    }
  },
  created() {
    this.getproduct();
    this.getAllProducts();
    this.getCart();
  },
  components: {
    Customcart,
    Pagination,
  }
};
</script>

<style>
.imghover :hover{
  cursor: pointer;
}
.sidebar{
  z-index: 0;
  box-shadow: none;
}
.list-group-item{
  background-color: #f3f3f3
}
.side-li{
  padding: 15px 0 15px 7px;
}
.side-li:hover , .active{
background-color: #00d96c;
border-radius: 5px;
    color: white;
}
@media (max-width: 567px)
{
[role="main"]{
      clear: both !important;
    position: relative !important;
    left: 0px !important;
}
}
@media (min-width: 576px)
{
.ml-sm-auto, .mx-sm-auto {
    margin-left: auto!important;
    flex: 0 0 75%;
    max-width: 65%;
}
}
@media (min-width: 992px)
{
.col-lg-9 {
    -ms-flex: 0 0 75%;
    flex: 0 0 75%;
    max-width: 75% !important;
}
}
</style>


