<template>
  <div>
    <Navbar></Navbar>

    <loading :active.sync="isLoading"></loading>

    <div class="container main-contant py-3 pt-5">
      <h2 class="text-center mt-5">確認結帳</h2>
      <section class="form-row align-items-center text-center d-none d-md-flex">
        <!-- 購物籃有商品 -->

        <div class="col-md-8 col-10 mt-0 mt-md-5 mx-auto" v-if="cart.length !=0">
          <form id="needs-validation" novalidate>
            <table class="table mt-4 center">
              <thead>
                <tr class="rounded border bg-light">
                  <th width="80"></th>
                  <th width="100"></th>
                  <th width="200">品名</th>
                  <th width="120">數量</th>
                  <th width="120">單價</th>
                  <th width="120">小計</th>
                </tr>
              </thead>

              <tbody>
                <tr v-for="item in cart" :key="item.id">
                  <td class="align-middle">
                    <button
                      type="button"
                      class="btn btn-outline-danger btn-sm"
                      @click="removeModal(item)"
                    >
                      <i class="far fa-trash-alt"></i>
                    </button>
                  </td>
                  <img :src="item.product.imageUrl" class="img-fluid img-thumbnail table-mobile-img">
                  <td class="align-middle">
                    {{ item.product.title }}
                    <div class="text-success" v-if="item.coupon">已套用優惠券</div>
                  </td>
                  <td class="align-middle">{{ item.qty }} {{ item.product.unit }}</td>
                  <td class="align-middle">{{ item.total | currency}}</td>
                  <td class="align-middle">{{ item.final_total | currency}}</td>
                </tr>
              </tbody>
              <tfoot>
                <tr>
                  <td colspan="5" class="text-right font-weight-bold">總計</td>
                  <td class="text-right font-italic font-weight-bold">{{ total | currency }}</td>
                </tr>
                <tr v-if="total !== finalTotal">
                  <td colspan="5" class="text-right text-success font-weight-bold">折扣價</td>
                  <td
                    class="text-right text-success font-weight-bold font-italic"
                  >{{ finalTotal | currency }}</td>
                </tr>
              </tfoot>
            </table>
            <div class="input-group mb-3 input-group-sm">
              <input type="text" class="form-control" placeholder="請輸入優惠碼" v-model="salecode">
              <div class="input-group-append">
                <button class="btn btn-outline-secondary" type="button" @click="addsalecode">套用優惠碼</button>
              </div>
            </div>

            <div class="text-right">
              <router-link to="/customer/customer_order" class="btn btn-secondary mr-3">繼續選購</router-link>
              <button type="submit" class="btn btn-primary mr-3" @click.prevent="pay()">確認付款</button>
            </div>
          </form>
        </div>

        <!-- 購物籃無商品 start -->
        <div class="col-md-8 mx-auto col-10" v-else>
          <h5 class="py-3 mt-2 mt-md-5 mb-2 text-center bg-light">購物車暫無商品</h5>
          <div class="text-right">
            <router-link to="/customer/customer_order" class="btn btn-secondary mt-4">回商店選購</router-link>
          </div>
        </div>
      </section>

      <!-- 手機  -->
      <div class="d-block d-md-none py-3 mb-2 ">
      <h5 class="d-block d-md-none py-3 mb-2 text-center bg-light">購物車細節</h5>
      <table class="table table-sm d-table d-md-none">
        <tbody>
          <tr v-for="(item,index) in cart" :key="index">
            <td class="align-middle text-center" width="30">
              <a href="#" class="text-muted" data-toggle="modal" @click.prevent="removeModal(item)">
                <i class="fas fa-trash-alt" aria-hidden="true"></i>
              </a>
            </td>
            <td class="align-middle" width="60">
              <img :src="item.product.imageUrl" class="img-fluid img-thumbnail table-mobile-img">
            </td>
            <td class="align-middle" width="100">
              <router-link :to="{path: 'product/'+item.product.id}">{{item.product.title}}</router-link>
              <div class="text-muted" style="font-size:0.8rem">
                數量：{{item.qty}} {{item.product.unit}}
                <br>
                單價：{{item.product.price | currency}}
                <br>
                小計：{{item.final_total | currency}}
                <br>
              </div>
            </td>
          </tr>
          <tr>
            <td colspan="3" class="text-right font-weight-bold">
              <strong>總計 &nbsp; ${{total}}</strong>
            </td>
          </tr>
          <tr v-if="total !== finalTotal">
                  <td colspan="3"
                    class="text-right text-success font-weight-bold font-italic"
                  >折扣價 {{ finalTotal | currency }}</td>
                </tr>
        </tbody>
      </table>

      <div class="input-group mb-3 input-group-sm">
        <input type="text" class="form-control" placeholder="請輸入優惠碼" v-model="salecode">
        <div class="input-group-append">
          <button class="btn btn-outline-secondary" type="button" @click="addsalecode">套用優惠碼</button>
        </div>
      </div>
      <div class="text-right">
        <router-link to="/customer/customer_order" class="btn btn-secondary mr-3">繼續選購</router-link>
        <button type="submit" class="btn btn-primary mr-3" @click.prevent="pay()">確認付款</button>
      </div>
    </div>
      <!--del modal start-->
      <div
        class="modal fade"
        id="removeModal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header bg-danger text-white">
              <h5 class="modal-title">刪除商品</h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <p v-if="tempItem.product">
                確認刪除
                <span class="text-success">{{tempItem.product.title}}</span> ?
              </p>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">否</button>
              <button
                type="button"
                class="btn btn-outline-danger px-5"
                @click.prevent="delItem()"
                :class="{'disabled':status.del}"
              >
                確認
                <i class="fas fa-spinner fa-spin pl-2 ml-1" v-if="status.del"></i>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Navbar from "../Navbar";

export default {
  props: ["step", "userdata"],
  components: {
    Navbar
  },
  data() {
    return {
      cart: [],
      carts: [],
      salecode: "",
      total: 0,
      finalTotal: 0,
      tempItem: {},
      isLoading: false,
      status: {
        del: false
      }
    };
  },
  methods: {
    getCartList() {
      var vm = this;
      vm.isLoading = true;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      this.$http.get(api).then(response => {
        vm.isLoading = false;
        vm.cart = response.data.data.carts;
        vm.total = response.data.data.total;
        vm.finalTotal = response.data.data.final_total;
      });
    },
    removeModal(item) {
      $("#removeModal").modal("show");
      this.tempItem = item;
    },
    delItem() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${
        this.tempItem.id
      }`;
      this.status.del = true;
      this.$http.delete(api).then(response => {
        this.status.del = false;
        $("#removeModal").modal("hide");
        this.tempDel = {};
        this.getCartList();
      });
    },
    pay() {
      var vm = this;
      this.$validator.validate().then(result => {
        if (result) {
          this.$router.push(`/customer/confirm/keyin`);
          this.$emit("step", (this.currentStep = "pay"));
        } else {
          // console.log("資料有誤")
        }
      });
    },
    delcart(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(url).then(response => {
        // console.log(response)
        this.getCart();
        vm.isLoading = false;
      });
    },
    addsalecode() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
      const coupon = { code: vm.salecode };
      //讓API回傳的code=設定的變數

      vm.isLoading = true;
      this.$http.post(url, { data: coupon }).then(response => {
        console.log(response);
        console.log(response.data.message);
        if (response.data.success == false) {
          this.$bus.$emit("message:push", response.data.message, "danger");
          // alert(response.data.message)
        }
        vm.getCartList();
        vm.isLoading = false;
      });
    }
  },
  created() {
    this.getCartList();
    this.$emit("step", (this.currentStep = "info"));
    $(".navbar-nav").css("marginRight", "0px");
  }
};
</script>

<style>
table a:hover {
  text-decoration: none;
}
.table-mobile-img {
  width: 100%;
  max-width: 120px;
  height: 80px;
  background-size: cover;
  background-position: center;
}
</style>


