<template>
    <div>
        <loading :active.sync="isLoading"></loading>

    <table class="table mt-5">
      <thead>
        <tr>
          <th width="150">購買時間</th>
          <th>Email</th>
          <th width="300">購買款項</th>
          <th width="150">應負金額</th>
          <th width="150">是否付款</th>
                  </tr>
      </thead>
       <tbody>
                <tr v-for="(item) in orders" :key="item.id">
                    <td>{{item.paid_date | date}}</td>
                    <td>{{ item.user.email }}</td>
                    <td> 
                      <ul class="list-unstyled">
                            <li v-for="(product, key) in item.products" :key="key">
                                {{ product.product.title }}， 共 {{ product.product.num }} {{ product.product.unit }}
                            </li>
                     </ul>
                    </td>
                    
                    <td class="text-midden">
                        {{ item.total | currency}}
                    </td>
                    <td>
                         <span v-if="item.is_paid" class="text-success">已付款</span>
                         <span v-else class="text-muted">尚未付款</span>
                    </td>
                </tr>
            </tbody>
    </table>
    
     <Pagination :pages="pagination" @emitPages="getorders"></Pagination>

    </div>
</template>

<script>
import $ from "jquery";
import Pagination from '../Pagination';

export default {
  data() {
    return {
      orders: [],
      tempProduct: {},
      isnew: false,
      isLoading: false,
      pagination: {},
      status: {
        fileloading: false
      }
    };
  },
  components: {
    Pagination,
  },
  methods: {
    getorders(page = 1) 
     {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/orders?page=${page}`; 
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api , vm.tempProduct).then((response) => {
          // console.log(response)
        vm.orders = response.data.orders;
        vm.pagination = response.data.pagination;
        // console.log(vm.orders);
        vm.isLoading = false;
      });
    },
 
  },
  created() {
    this.getorders();
  }
};
</script>


