<template>
  
    <div class="container main-contant py-3">
      <h2 class="text-center mb-3 mt-3 mb-1 mb-md-3">確認結帳</h2>
      <section class="form-row align-items-center text-center d-none d-md-flex">
        <div class="col">
          <div class="alert alert-rounded mb-0" role="alert" :class="{'alert-info  ':currentStep == 'pay','alert-light':currentStep !='pay'}">1.輸入訂單資料</div>
        </div>
        <div class="col">
          <div class="alert alert-rounded mb-0" role="alert" :class="{'alert-info ':currentStep == 'finish','alert-light':currentStep !='finish'}">2.付款完成</div>
        </div>
      </section>

      <router-view :step="currentStep" :userdata="data" @step="currentStep = $event"></router-view>
  </div>
  
</template>

<script>
import $ from 'jquery';
export default {
    data() {
      return {
        currentStep:'info',
        data:{
          user: {
            name: "",
            email: "",
            address: "",
            tel: ""
          },
          message:'',
          } 
        }
    },
    methods: {
    },
    created() {
    },
}
</script>


