<template>
    <div>
    <!-- 加入loading樣式 -->
    <loading :active.sync="isLoading"></loading>

    <div class="text-right mt-5">
      <!-- <button data-toggle="modal" data-target="#productModal">把bootstrap改成用vue連接</button> -->
      <button class="btn btn-primary" @click="open(true)">建立新的優惠券</button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="150">名稱</th>
          <th width="100">折扣百分比</th>
          <th width="100">到期日</th>
          <th width="80">是否啟用</th>
          <th width="80">編輯</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in coupons" :key="item.id">
          <td>{{item.title}}</td>
          <td>{{item.percent}}</td>
          <td>{{item.due_date}}</td>
          

          <td>
            <span v-if="item.is_enabled" class="text-success">啟用</span>
            <span v-else>未啟用</span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" @click="open(false,item)">編輯</button>
            <!-- <button class="btn btn-outline-danger btn-sm" @click="deletModal(item)">刪除</button> -->
          </td>
        </tr>
      </tbody>
    </table>
<!-- 引入分頁 -->
    <Pagination :pages="pagination" @emitPages="getcoupons" v-if="pagination"></Pagination>
<!-- 新增的Modal -->
    <div
      class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0 ">
          <div class="modal-header ">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>新增優惠碼</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          
          <div class="modal-body">
            <div class="row">
              
              <div class="col-sm-12">
                <div class="form-group">
                  <label for="title">標題</label>
                  <!-- <input
                    type="text" class="form-control" id="title" placeholder="請輸入標題"> -->
                    <input
                    type="text" class="form-control" id="title"
                    v-model="tempCoupons.title" placeholder="請輸入標題">
                </div>
                
                  <div class="form-group">
                    <label for="code">優惠碼</label>
                    <input
                      type="text" class="form-control" id="code"
                      v-model="tempCoupons.code"
                      placeholder="請輸入優惠碼">
                  </div>
                  <div class="form-group">
                    <label for="number">折扣百分比</label>
                    <input
                      type="text" class="form-control" id="percent"
                      v-model="tempCoupons.percent"
                      placeholder="請輸入折扣百分比">
                  </div>

                  <div class="form-group">
                    <label for="due_date">到期日</label>
                    <input type="date" class="form-control" id="due_date"
                      v-model="tempCoupons.due_date"
                      placeholder="請輸入到期日">
                  </div>
                

                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox" id="is_enabled"
                      v-model="tempCoupons.is_enabled"
                      :true-value="1"
                      :false-value="0" >
                       <!-- <input class="form-check-input" type="checkbox" id="is_enabled"> -->
                    <label class="form-check-label" for="is_enabled">是否啟用</label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer col-sm-12">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消更新</button>
            <!-- <button type="button" class="btn btn-primary" >確認更新</button> -->
            <button type="button" class="btn btn-primary" @click="newcoupon">確認更新</button>
          </div>
        </div>
      </div>
    </div>

    </div>
</template>

<script>
import $ from "jquery";
import Pagination from '../Pagination';
export default {
  data() {
    return {
      coupons: [],
      tempCoupons:{},
      isnew: false,
      isLoading: false,
      pagination: {},
      };
  },
  components: {
            Pagination
        },
  methods: {
    getcoupons(page = 1) 
    {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`; 
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        console.log(response.data);
        vm.coupons = response.data.coupons;
        vm.isLoading = false;
      });
    },
     open(isnew, item) {
      if (isnew) {
        (this.tempCoupons = {}), (this.isnew = true);
      } else {
        (this.tempCoupons = Object.assign({}, item)), (this.isnew = false);
      }
      //由於物件傳參考特性，tempProduct=item的話兩個值會一樣，所以使用Object.assign（es6的方法），將item值寫到空物件避免傳參考特性
      $("#productModal").modal("show");
      //此處$未定義，需使用import導入 jquery
    },
    newcoupon(id){
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
      const vm = this;
      // 因新增與編輯共用MODAL，故使用條件選擇為編輯還是新增
      let httpmethod = "post";

      if (!vm.isnew) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupons.id}`;
        httpmethod = "put";
      }
      
      this.$http[httpmethod](api, { data: vm.tempCoupons }).then(response => {
        console.log(response.data);
        if (response.data.success) {
          $("#productModal").modal("hide");
          vm.getcoupons();
        } else {
          $("#productModal").modal("hide");
          vm.getcoupons();
          console.log("新增失敗");
        }
        
      });
    },

    

   },
  created() {
    this.getcoupons();
  }
};
</script>


