// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import 'bootstrap';
// import VeeValidate from 'vee-validate';
import zhTWValidate from 'vee-validate/dist/locale/zh_TW'; //導入中文化界面
import VeeValidate, { Validator } from 'vee-validate';

import App from './App';
import router from './router';
import './bus';
import currencyfilter from './filters/currency'
import date from './filters/date'


axios.defaults.withCredentials = true;
Vue.config.productionTip = false;

Validator.localize('zh_TW',zhTWValidate);Vue.use(VeeValidate, {  locale:'zh_TW',});
Vue.use(VeeValidate);
// VeeValidate.Validator.localize('zh_TW',zhTWValidate) //導入中文化界面

Vue.use(VueAxios, axios);
Vue.component('Loading',Loading);
Vue.filter('currency',currencyfilter);
Vue.filter('date',date);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
});

router.beforeEach((to, from, next) => {
  // console.log('to ' ,to , 'from ', from , 'next ', next);
  if(to.meta.requiresAuth){
    // console.log('請登入')
    const api = `${process.env.APIPATH}/api/user/check`; 
    //this.$http.post(api).因為在router底下，不是在Vue底下，要換成axios
    axios.post(api).then(response => {
      // console.log(response.data);
      if(response.data.success) {
       next();
      }else{
      next({
        path:'/login',
      });
          }
           });
  }else{
    next();
  }
})



