<template>
<div>
    <Navbar></Navbar>
  
  <form class="form-signin mt-5" @submit.prevent="signin">
      <h1 class="h3 mb-3 font-weight-normal mt-5">請進行登入</h1>
      <label for="inputEmail" class="sr-only">Email address</label>
      <input type="email" id="inputEmail" class="form-control" v-model="user.username" placeholder="Email address" required autofocus>
      <label for="inputPassword" class="sr-only">Password</label>
      <input type="password" id="inputPassword" class="form-control" v-model="user.password" placeholder="Password" required>
      <div class="checkbox mb-3">
        <label>
          <input type="checkbox" value="remember-me"> Remember me
        </label>
      </div>
      <button class="btn btn-lg btn-primary btn-block" type="submit">Sign in</button>
      <p class="mt-5 mb-3 text-muted">&copy; 2018-2019 練習用網頁</p>
  </form>
    </div>
</template>

<script>
import Navbar from '../Navbar';

export default {
   components:{
      Navbar,
        },
  data () {
    return {
        user:{
      username:'',
      password:'',
      link: 'a',
      }
    }
  },

  
 methods: {
    signin(){
    const api = `${process.env.APIPATH}/admin/signin`; 
    const vm=this;
    this.$http.post(api,vm.user).then(response => {
    //   console.log(response.data);
      if(response.data.success) {
       vm.$router.push('/admin/products')
      }
      });
    },
       signout(){
    const api = `${process.env.APIPATH}/logout`; 
    const vm=this;
    this.$http.post(api).then(response => {
    //   console.log(response.data);
      if(response.data.success) {
       vm.$router.push('/login')
            }
         });
      }
 }
}
</script>

<style scoped>
html,
body {
  height: 100%;
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #f5f5f5;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
</style>


