<template>
  <div class="container">
     <div class="dropdown">
      <Customcart/>
    </div>
    <loading :active.sync="isLoading" loader="dots" :can-cancel="true" :is-full-page="fullPage"></loading>

    <div class="container text-white mb-5 my-auto">
      <div class="row">
        <div class="col-md-6">
          <ol class="breadcrumb" style="background:none;">
            <li class="breadcrumb-item">
              <router-link to="#" class="text-dark">首頁</router-link>
            </li>
            <li class="breadcrumb-item">
              <router-link to="/customer/customer_order" class="text-dark">購物專區</router-link>
            </li>
            <li class="breadcrumb-item text-info font-weight-bold">
              {{ product.title }}
            </li>
          </ol>
        </div>
        <div class="col-md-4 text-center">
        </div>
      </div>

      <div class="row">
        <div class="col-md-6 mx-auto my-auto">
          <img :src="product.imageUrl" class="img-fluid rounded" alt="img">
        </div>
        <div class="col-md-5 bg text-dark">
          <h3>{{product.title}} <div class="pro-title  text-center ml-3"> {{product.category}}</div>
          </h3>
          <h5 class="my-4 mylh">{{product.content}}</h5>
          <p class="text-right text-dark">--{{product.description}}</p>
          <div class="d-flex justify-content-between align-items-baseline mb-3 mt-5 text-dark">
            <del class="h6">原價 {{product.origin_price}} 元</del>
            <div class="h4 text-danger">現正特惠價 {{product.price}} 元</div>
          </div>
          <select class="form-control mt-5" v-model="optionNum">
            <option v-for="(i, index) in 8" :key="index" :value="i">選購{{ i }}件</option>
          </select>
          <div class="my-5 d-flex justify-content-between align-items-baseline">
              <span class="pr-3 text-white ml-4 h4 text-dark">合計 {{ optionNum * product.price }} 元</span>
              <button type="button" class="btn btn-warning" @click="addtoCart(product,optionNum)">加到購物車</button>
          </div>
          
        </div>
      </div>
    </div>
    <div class="container mt-3">
    <p class="float-right text-dark"><a href="#" style="position:sticky; top:50px;"></a></p>
    <p class="text-center text-dark">&copy; 2019 Tim - 練習用網頁 </p>
  </div>
  </div>
</template>

<script>
  import Customcart from './Customcart';
  
  export default {
    components:{
      Customcart,
    },
    data() {
      return {
        isLoading: false,
        fullPage: true,
        product: {},
        optionNum: '1',
      }
    },
    methods: {
      addtoCart(item, qty = 1) {
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
        const vm = this;

        vm.isLoading = true;

        // console.log(item)
        const cart = {
            product_id: item.id,
            qty,
        };
       this.$http.post(api, { data: cart }).then(response => {
        vm.isLoading = false;
        // console.log(response)
         this.$bus.$emit("message:push", response.data.message, "success");
        // // vm.getCart();
        // $("#productModal").modal("hide");
      });
        

      },
    },

    
    created() {
      const vm = this;
      vm.isLoading = true;
      const api =`${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${vm.$route.params.productId}`;
      this.$http.get(api).then((res) => {
        vm.product = res.data.product;
        vm.isLoading = false;
      });
    }
  }
</script>

<style>
  .pro-title {
    background: rgb(252, 214, 111);
    display: inline-block;
    font-size: 20px;
    width: 70px;
    height: 40px;
    border-radius: 10px;
    line-height: 40px;
    color: rgb(66, 80, 71);
  }
  .bg{
    background: hsl(155, 100%, 80%);
    border-radius: 5px;
    padding-top: 30px;
  }
  .mylh{
    line-height: 40px;
  }
</style>


