<template>
<div>
  <nav class="navbar navbar-expand-md navbar-dark fixed-top" style="background-color:#8ec1cd;">
    <a class="navbar-brand pl-2 pr-5" href="#/home" style="background-color:#8ec1cd;box-shadow:none;border-radius:5px;">
      <img src="../assets/hills.png" class="img-fluid mr-2" alt="Responsive image" style="width:20%; height:20%; float:left;"><h5 class="mt-1">好空氣，布希嗎？</h5></a>
   
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarCollapse" aria-controls="navbarCollapse" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarCollapse">
      <ul class="navbar-nav mr-auto">
        <li class="nav-item " >
          <a class="nav-link font-weight-bold line" active-class="active" href="#/home">首頁 <span class="sr-only">(current)</span></a>
        </li>
        <li class="nav-item font-weight-bold" active-class="active">
          <router-link class="nav-link line"  to="./customer/customer_order">商品選購</router-link>
        </li>
      </ul>
      <ul class="navbar-nav navbar-right"> 
      <li class="nav-item text-nowrap ">
          <a class="nav-link font-weight-bold line"  href="#" @click.prevent="signout">後台登入</a>
        </li>
    </ul>
      </div>
   </nav>   
    </div>
  

</template>

<script>
export default {
  data () {
    return {}
    },
    methods: {
      signout(){
    const api = `${process.env.APIPATH}/logout`; 
    const vm=this;
    this.$http.post(api).then(response => {
    //   console.log(response.data);
      if(response.data.success) {
       vm.$router.push('/login')
            }
         });
      }
  }
}
</script>

<style>
.nav-item{
  padding:8px 40px 8px 40px;
}
.navbar{
  z-index: 1;
}
.navbar-right{
  margin-right: 100px !important;
}

 .line{
   display: inline-block;
   position: relative;
   overflow: hidden;
    }
  .line:before{
      padding-bottom: 1px;
      content: "";
      position: absolute;
      left: 50%;
      right: 50%;
      bottom: 0;
      background: #ff9d2e;
      height:3px;
      transition-property: left, right;
      transition-duration: 0.3s;
      transition-timing-function: ease-out;
    }
    .line:hover:before, .line:focus:before, .line:active:before {
        left: 0;
        right: 0;
    }
    
</style>



