import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/components/Home';
import Dashboard from '@/components/Dashboard';
import Customboard from '@/components/Customboard';
import Products from '@/components/Products';

import Coupon from '@/components/pages/Coupon';
import Orders from '@/components/pages/Orders';
import Login from '@/components/pages/login';
import customerorder from '@/components/pages/Customerorder';
import detail from '@/components/pages/DetailCard';
import CustomerPay from '@/components/pages/CustomerPay';
import CustomConfirm from '@/components/pages/CustomConfirm';
import CustomChick from '@/components/pages/CustomChick';
import CustomKeyin from '@/components/pages/CustomKeyin';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'*',
      redirect:'login',
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
    {
      path: '/admin',
      name: 'Dashboard',
      component: Dashboard,
      meta: { requiresAuth: true },
      children:[
      {
        path: 'products',
        name: 'Products',
        component: Products,
        meta: { requiresAuth: true }
      },
      {
        path: 'coupon',
        name: 'coupon',
        component: Coupon,
        meta: { requiresAuth: true }
      },
      {
        path: 'orders',
        name: 'orders',
        component: Orders,
        meta: { requiresAuth: true }
      },
    ],
    },
    //客戶端
    {
      path: '/chick',
      name: 'CustomChick',
      component: CustomChick,
    },
    {
      path: '/customer',
      name: 'test',
      component: Customboard,
      children:[
      {
        path: 'customer_order',
        name: 'customer',
        component: customerorder,
      },
      {
        path:'detail/:productId',
        name:'detail',
        component:detail,
      },
      {
        path: 'confirm',
        name: 'CustomConfirm',
        component: CustomConfirm,
        children:[ 
         {
            path: 'keyin',
            name: 'CustomKeyin',
            component: CustomKeyin,
         },
        {
        path: 'pay/:orderId',
        name: 'CustomerPay',
        component: CustomerPay,
        },
        ]
      },
      
    ],
    }
  ]
})



