<template>
    <div>
        <nav class="col-md-2 d-none d-md-block mt-5 cusbar" >
            <div class="list-group list-group-flush">
              <span data-feather="home" class="nav-link fas text-info pb-3"><i class="fas fa-box"> 商品列表 </i></span>
              <a href="#" class="list-group-item nav-link side-li" data-toggle="list" 
                @click.prevent="currentPage ='全部',status.filter=false">
                 全部
              </a>
              <a href="#" class="list-group-item nav-link side-li" data-toggle="list"
                @click.prevent="currentPage ='台灣',status.filter=true ">
                台灣
              </a>
              <a href="#" class="list-group-item nav-link side-li" data-toggle="list"
                @click.prevent="currentPage ='國外',status.filter=true" >
                國外
              </a>
              <a href="#" class="list-group-item nav-link side-li" data-toggle="list"
                @click.prevent="currentPage ='其他',status.filter=true" >
                其他
              </a>
            </div>
        </nav>
     </div>

</template>

<script>

export default {
  data() {
    return{
       allProducts:[],
      products: [],
    currentPage: "全部",
      pagination: {},
      isLoading: false,
       status: {
        addToCart: "",
        filter:false,
      },
      }
    },
methods: {

    getAllProducts(){
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/products`;
      this.$http.get(api).then(response => {
        this.allProducts=response.data.products;
      });
    },
     },
computed: {
    filterProducts() {
      var vm = this;
      
      if (vm.currentPage == "全部") {
        return this.products;
      }
      else{
        return this.allProducts.filter(function(item, index) {
          // console.log(vm.currentPage)
          if (item.category == vm.currentPage) return true;
        });
      }
    }
  },
   created() {
    this.getAllProducts();
  },
}
</script>

<style>
.sidebar{
  z-index: 0;
  box-shadow: none;
}
.side-li{
  padding: 15px 0 15px 7px;
}
.side-li:hover , .active{
background-color: #00d96c;
border-radius: 5px;
    color: white;
}
</style>


