<script>
import Navbar from './Navbar';
import CustomSidebar from './CustomSidebar';
import Sidebar from './Sidebar';
import Alert from './Alert';

export default {
    components:{
        CustomSidebar,
        Navbar,
        Sidebar,
        Alert,
    },
};
</script>

